import React from "react";
import { createRoot } from "react-dom/client";
import { SpinningText } from "./SpinningText";

function App() {
  return (
    <div style={{ width: "100%", height: "100%" }}>
      <SpinningText duration={12} radius={6} className="spinning-text" style={{ width: "100%", height: "100%" }}>
        VIRELIA: ECHOES OF THE HOLLOW BLOOM
      </SpinningText>
    </div>
  );
}

const mount = document.getElementById("spinning-root");
if (mount) {
  const root = createRoot(mount);
  root.render(<App />);
}
